:root {
  --color-primary: #000000;
  --color-primary-disabled: rgb(225,225,225);
  --color-secondary: #FFFFFF;
  --color-highlight: #FFE600;
  --gap-xsmall: 1.375em;
  --gap-small: 1.75em;
  --gap-normal: 2.375em;
  --gap-big: 2.75em;
  --section-gap: 2em;
  --font-primary: 'Roboto Condensed', sans-serif;
  --font-secondary: 'Rasa', serif;
  --font-headings: 'Roboto Condensed', sans-serif; }

html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
div,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  box-sizing: border-box;
  display: block;
  line-height: 1.5; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:is-active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

a {
  color: var(--color-primary);
  text-decoration: none; }

.row {
  --row-max-width: 72.5em;
  max-width: var(--row-max-width);
  margin-left: auto;
  margin-right: auto; }
  .row-slim {
    --row-max-width: 42.5em; }

.is-marked {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 50%, var(--color-highlight) 50%); }

.responsive-image {
  overflow: hidden;
  padding-bottom: 100%;
  width: 100%;
  height: 0;
  display: inline-block;
  position: relative; }
  .responsive-image img {
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover; }
  .responsive-image.cropped-center {
    text-align: center; }
    .responsive-image.cropped-center img {
      width: auto; }
  .responsive-image.ratio-2-1 {
    padding-bottom: 50%; }
  .responsive-image.ratio-1-2 {
    padding-bottom: 200%; }
  .responsive-image.ratio-590-394 {
    padding-bottom: 66.77966%; }
  .responsive-image.rounded-small {
    border-radius: .25em; }

.navlist {
  list-style: none;
  margin: 0;
  padding: 0; }
  .navlist-inline {
    display: inline; }
    .navlist-inline .navlist-item {
      display: inline; }
  .navlist a {
    color: inherit; }

.site-header .navlist {
  font-family: var(--font-primary); }
  .site-header .navlist-item {
    margin: 0 2.75em;
    font-weight: 700; }
    @media screen and (max-width: 54.875em) {
      .site-header .navlist-item {
        margin: 0 1.5em; } }
    @media screen and (max-width: 40em) {
      .site-header .navlist-item {
        display: block;
        font-size: 1.25em;
        margin: 0.75em auto; } }

.button {
  --button-color-bg: var(--color-secondary);
  --button-color-bg-hover: var(--color-secondary);
  --button-color: var(--color-primary);
  --button-padding: 0.875em 1.5em;
  --button-border-color: var(--button-color-bg);
  --button-border-radius: var(--global-border-radius);
  background: var(--button-color-bg);
  border-radius: var(--button-border-radius);
  border: 2px solid var(--button-border-color);
  display: inline-block;
  padding: var(--button-padding);
  color: var(--button-color);
  font-family: var(--font-primary);
  font-weight: 700;
  line-height: 1.25;
  text-decoration: none;
  opacity: 1;
  transition: all 250ms ease;
  cursor: pointer; }
  .button:hover {
    opacity: .5; }
  .button svg {
    fill: var(--button-color); }
  .button-highlighted {
    box-shadow: 0.625em 0.625em 0 #FFE600;
    margin-bottom: 0.625em;
    margin-right: 1.25em; }
  .button-outline {
    background-color: transparent;
    border-color: var(--button-color); }
  .button-underline {
    background-color: transparent;
    border: transparent;
    border-bottom: 2px solid var(--button-color);
    padding: 0.5em 0; }
  .button-arrow {
    display: inline-flex;
    align-items: center; }
    .button-arrow svg {
      margin-left: 1em; }
  .button-icon {
    padding: 0;
    width: 2.625em;
    height: 2.625em;
    display: inline-flex;
    justify-content: center;
    align-items: center; }

.entry {
  color: var(--color-primary); }
  .entry-title {
    font-family: var(--font-headings);
    font-weight: bold;
    font-size: 1.625em;
    line-height: 1.15;
    text-transform: uppercase; }
  .entry-content, .entry-author {
    font-family: var(--font-secondary);
    font-size: 1.125em; }
  .entry-tags {
    position: relative;
    padding-bottom: 1.5em; }
    .entry-tags:after {
      display: block;
      content: '';
      width: 100%;
      height: 0.75em;
      position: absolute;
      bottom: 0;
      left: 0;
      background: url(../images/line-2.png) repeat-x;
      transform: skewX(-45deg); }
  .entry-tag {
    font-family: var(--font-primary);
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 0.1em;
    margin-right: 1.875em; }
    .entry-tag.is-disabled {
      opacity: 0.5;
      margin-right: 2.625em; }
    @media screen and (max-width: 48em) {
      .entry-tag {
        margin-right: .5em; }
        .entry-tag.is-disabled {
          margin-right: .625em; } }
  .entry-author-name {
    font-weight: 700; }

.entries-highlights .entries-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.625em; }

.entries-highlights .entry {
  position: relative; }
  .entries-highlights .entry-title {
    text-align: center;
    position: absolute;
    max-width: 100%;
    left: 0;
    bottom: 0; }
    .entries-highlights .entry-title a {
      display: inline-block;
      box-sizing: border-box;
      width: 100%;
      padding: 0 1.25em; }
    .entries-highlights .entry-title span {
      background: #FFF;
      padding: 0 0.25em;
      box-decoration-break: clone; }
    @media screen and (max-width: 64em) {
      .entries-highlights .entry-title {
        font-size: 1.25em;
        margin-bottom: 0.25em; }
        .entries-highlights .entry-title a {
          padding: 0 0.675em; } }

@media screen and (max-width: 64em) {
  .entries-highlights {
    overflow: hidden; }
    .entries-highlights .entries-container {
      display: flex;
      flex-wrap: nowrap;
      flex-direction: row;
      animation-timing-function: linear;
      animation-iteration-count: infinite;
      animation-duration: 10s;
      animation-name: slideShow; }
    .entries-highlights .entry {
      flex: 0 0 100%; } }

.entries-readmore .entry {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.625em;
  margin-top: 4em; }
  .entries-readmore .entry-thumbnail {
    position: relative; }
    .entries-readmore .entry-thumbnail-image {
      box-shadow: 0 2.125em 0 -1.375em #000; }
    .entries-readmore .entry-thumbnail-triangles {
      width: 4.3125em;
      transition: 0.25s all linear;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 5; }
      .entries-readmore .entry-thumbnail-triangles-box {
        position: relative;
        background: #FFFFFF;
        width: 100%;
        height: 0;
        padding-bottom: 100%; }
        .entries-readmore .entry-thumbnail-triangles-box:before {
          content: '';
          position: absolute;
          background: #000;
          width: 100%;
          height: 100%;
          left: 1.375em;
          top: 1.375em;
          display: block;
          clip-path: polygon(0% 0%, 0% calc(100% - 1.375em), calc(100% -  1.375em) 0%); }
        .entries-readmore .entry-thumbnail-triangles-box:after {
          content: '';
          position: absolute;
          background: var(--color-highlight);
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          clip-path: polygon(100% 100%, 100% 0%, 0% 100%); }
    .entries-readmore .entry-thumbnail:hover .entry-thumbnail-triangles {
      width: 5.25em; }
  @media screen and (max-width: 48em) {
    .entries-readmore .entry {
      grid-template-columns: 1fr; }
      .entries-readmore .entry-thumbnail {
        grid-row-start: 1; } }

.section-fullwidth {
  padding: var(--section-gap); }

